class TaskManager {
  constructor() {
    this.tasks = [];
    this.pendingTask = [];
  }

  getTasks() {
    return this.tasks;
  }

  addTask(obj) {
    this.tasks.push(new Task(obj));
  }

  pickTask() {
    return this.tasks.shift();
  }

  modifyTaskPriority(property, value, priority) {
    this.tasks = this.tasks.map((task) => {
      if (task?.[property] === value) {
        task.priority = priority;
      }

      return task;
    });

    this.sort(this.tasks);
  }

  sort(tasks) {
    tasks.sort((a, b) => a.priority - b.priority);
  }

  // getTask(seriesId, plane, index) {
  //   const task = this.tasks.find((task) => {
  //     return task.seriesId === seriesId && task.plane === plane && task.index === index;
  //   });
  //   return task;
  // }

  /**
   * åæ°é¡ºåºä¸º,seriesId, plane, index å¯ä»¥é½ä¸ä¼ 
   *
   * @param {*} args
   * @return {*}
   * @memberof TaskManager
   */
  getTask(...args) {
    const { length } = args;
    const [seriesId, plane, index] = args;
    const fn0 = (task) => task;
    const fn1 = (task) => task.seriesId === seriesId;
    const fn2 = (task) => task.seriesId === seriesId && task.plane === plane;
    const fn3 = (task) =>
      task.seriesId === seriesId && task.plane === plane && task.index === index;
    const sortFn = [fn0, fn1, fn2, fn3];

    return this.getTasks().filter(sortFn[length]);
  }

  hasPendingTask() {
    return this.pendingTask.length > 0;
  }

  /**
   *
   *
   * @param { Task } task
   * @memberof TaskManager
   */
  addPendingTask(task) {
    // æéçæ¶åï¼åå¨pendingéåéæ¾ä¸ä¸ï¼æ¯å¦å·²ç»å­å¨å¯¹åºtaskï¼å¦ææçè¯ï¼æ´æ°taskçä¼åçº§ãå¦åæå¥æ°çtask
    const existIndex = this.pendingTask.findIndex((t) => {
      return t.plane === task.plane && t.seriesId === task.seriesId && t.index === task.index;
    });

    if (existIndex !== -1) {
      const oldTask = this.pendingTask[existIndex];
      oldTask.priority = Date.now();
      this.pendingTask.splice(existIndex, 1, oldTask);
    } else {
      task.priority = Date.now();
      this.pendingTask.push(task);
    }

    this.sort(this.pendingTask);
  }
}

/**
 * Task
 *
 * @class Task
 */
class Task {
  constructor(obj) {
    const { url = "", seriesId = "", plane = "", index, format = "dicom" } = obj;
    this.priority = 0;
    this.imageUrl = url;
    this.seriesId = seriesId;
    this.plane = plane;
    this.index = index;
    this.format = format;
  }
}

export default TaskManager;
