let canvas = document.createElement("canvas");
// å ä¸ºworkeræ æ³æä½domï¼å½å è½½jpg,pngçæ¶åï¼å°arrayBuffer è¿åï¼æå»ºå½±å
export const postprocessor = async (image, task) => {
  return new Promise((resolve, reject) => {
    let img = image;
    const { format } = task;
    if (format === "webimage") {
      img = {};
      let tempImg = new Image();
      var arrayBufferView = new Uint8Array(image.data);
      var blob = new Blob([arrayBufferView], { type: "image/jpeg" });
      const urlCreator = window.URL || window.webkitURL;
      const imageUrl = urlCreator.createObjectURL(blob);
      tempImg.src = imageUrl;
      // document.body.append(tempImg);
      tempImg.onload = (evt) => {
        img.columns = tempImg.width;
        img.rows = tempImg.height;
        img.color = "rgba";
        img.samplesPerPixel = 4;
        img.windowWidth = 255;
        img.windowCenter = 128;
        img.maxPixelValue = 255;
        img.minPixelValue = 0;
        img.intercept = 0;
        img.slope = 1;

        canvas.width = tempImg.width;
        canvas.height = tempImg.height;
        const ctx = canvas.getContext("2d");
        ctx.drawImage(tempImg, 0, 0);
        img.pixelData = ctx.getImageData(0, 0, tempImg.width, tempImg.height).data;
        resolve(img);
      };
    } else {
      resolve(img);
    }
  });
};
