    columnPixelSpacing: pixelSpacing[1],
    // ç¼©æ¾æçåæªè·ç±ç¡¬ä»¶å¶é åå³å®ã
    // å®æå®ä»å­å¨å¨ç£çè¡¨ç¤ºä¸­çåç´ å°å­å¨å¨åå­è¡¨ç¤ºä¸­çåç´ ççº¿æ§è½¬æ¢ãç£çå­å¨çå¼å®ä¹ä¸ºSVãèè½¬åå°åå­ä¸­çåç´ å¼uintså°±éè¦ä¸¤ä¸ªdicom tag : Rescale interceptåRescale slopeã
