import { Group } from "konva/lib/Group";
import { activeUtil, cursor } from "../../tools/utils/index";

class UIComponent extends Group {
  constructor(config = {}) {
    super(config);

    if (config.useDefaultMouseEffect ?? true) {
      // ç»å®é¼ æ äº¤äºæ ·å¼ä¸å¤è§æ ·å¼ã
      this._bindMouseEffect();
    }
  }

  _bindMouseEffect() {
    let dragging = false;
    this.on("mouseover", (evt) => {
      cursor(this, "grab");
      activeUtil.on(this);
    });
    this.on("mousedown", (evt) => {
      cursor(this, "grabbing");
      dragging = true;
    });
    this.on("mouseout mouseleave", (evt) => {
      if (dragging) {
        return;
      }
      cursor(this);
      activeUtil.off(this);
    });
    this.on("dragend mouseup", (evt) => {
      cursor(this, "grab");
      dragging = false;
    });
  }
}

export default UIComponent;
